#single_MealDiv {
  width: 45%;
  height: 50vh;
  margin: 5px;
  border: 2px solid #000;
  float: left;
}
#single_MealDiv > h4 {
  position: absolute;
  background-color: rgba(14, 13, 13, 0.63);
}

#single_MealDiv:hover {
  cursor: pointer;
  transform: scale(1.02);
  border: 2px dashed #3805f0;
}
